// ** React Imports
import { useState } from 'react'

// ** Icons Imports
import { X, Plus } from 'react-feather'

// ** Custom Components
import Repeater from '@components/repeater'

// ** Reactstrap Imports
import { Row, Col, Label, Input, Button } from 'reactstrap'
import Cleave from 'cleave.js/react'

const RepeatingForm = () => {
  // ** State
  const [count, setCount] = useState(0)

  const increaseCount = () => setCount(count + 1)

  const deleteForm = e => {
    e.preventDefault()
    setCount(prev => prev - 1)
  }

  return (
    <div>
        <Repeater count={count} id={'additionalNumber'}>
            {i => (
                <Row key={i} className='justify-content-between align-items-center'>
                    <Col md={4} className='mb-md-0 mb-1'>
                        <Label className='form-label' for={`phone-${i}`}>
                            Дополнительный номер
                        </Label>
                        <Cleave
                            required
                            id={`phone-${i}`}
                            name={`phone-${i}`}
                            className='form-control'
                            placeholder='7-999-888-77-66'
                            options={{blocks: [1, 3, 3, 2, 2], delimiter: '-', numericOnly: true}}
                        />
                    </Col>
                    <Col md={6} className='mb-md-0 mb-1'>
                        <Label className='form-label' for={`comments-${i}`}>
                            Комментарии
                        </Label>
                        <Input type='text' id={`comments-${i}`} name={`comments-${i}`} placeholder='Папа \ Мама \ Сестра' />
                    </Col>
                    <Col md={2} style={{marginTop: '24px'}}>
                        <Button color='danger' className='text-nowrap px-1' onClick={deleteForm} outline>
                            <X size={10} className='me-50' />
                            <span>Удалить</span>
                        </Button>
                    </Col>
                    <Col sm={'12'}>
                        <hr />
                    </Col>
                </Row>
            )}
        </Repeater>
        <Button className='btn-icon' outline color='primary' onClick={increaseCount}>
          <Plus size={14} />
          <span className='align-middle ms-25'>Добавить номер</span>
        </Button>
    </div>
  )
}

export default RepeatingForm
