// ** React Imports
import { Fragment, useState, useEffect } from 'react'

// ** Third Party Components
import Select from 'react-select'
import Cleave from 'cleave.js/react'
import { useForm } from 'react-hook-form'
import 'cleave.js/dist/addons/cleave-phone.us'

// ** state
import { addUser, createUser } from '../store'
import { useDispatch } from 'react-redux'

// ** Style
import '@styles/react/libs/flatpickr/flatpickr.scss'

// ** Reactstrap Imports
import { Toast, ToastBody, ToastHeader, Row, Col, Form, Card, Input, Label, Button, CardBody, CardTitle, CardHeader, FormFeedback } from 'reactstrap'

// ** Utils
import { selectThemeColors } from '@utils'

// ** Components
import PickerHumanFriendly from './DateHumanPicker'
import RepeatPhoneForm from './RepeatPhoneForm'
import { Link } from 'react-router-dom'

// ** Demo Components
const sexOptions = [
  { value: 'M', label: 'Мужской' },
  { value: 'F', label: 'Женский' }
]

const AccountForm = () => {

  // ** empty data
  const data = {}
  const emptyAvatar = require('@src/assets/images/avatars/avatar-blank.png').default

  // ** on mount
  useEffect(() => {
    console.log('On mount')
  })

  // ** Hooks
  const defaultValues = {
    lastName: '',
    firstName: '',
    middleName: ''
  }

  const { control, setError, handleSubmit, formState: {  } } = useForm({ defaultValues })

  // ** States
  const dispatch =  useDispatch()
  const [avatar, setAvatar] = useState(data?.avatar ? data?.avatar : emptyAvatar)
  const [birthDate, setBirthDate] = useState(data?.birthDate ? data?.birthDate : new Date())
  const [sex, setSex] = useState(data?.sex ? data?.sex : sexOptions[0])
  const [phone, setPhone] = useState(data?.phone ? data?.phone : '')
  const [email, setEmail] = useState(data?.email ? data?.email : '')
  const [password, setPassword] = useState(data?.password ? data?.password : '')
  const [pasGen, setPasGen] = useState(data?.pasGen ? data?.pasGen : true)
  const [lastName, setLastName] = useState(data?.lastName ? data?.lastName : '')
  const [firstName, setFirstName] = useState(data?.firstName ? data?.firstName : '')
  const [middleName, setMiddleName] = useState(data?.middleName ? data?.middleName : '')
  const [errors, setErrors] = useState({})

  const onChange = e => {
    const reader = new FileReader(),
      files = e.target.files
    reader.onload = function () {
      setAvatar(reader.result)
    }
    reader.readAsDataURL(files[0])
  }

  const handleFormSubmit = async (ev) => {
    ev.preventDefault()

    const userData = {
      phone: phone.replace('_', ''),
      birth_date: birthDate.toISOString().split('T')[0],
      gender: sex.label,
      is_pass_generation: pasGen === true,
      last_name: lastName,
      first_name: firstName,
      middle_name: middleName,
      avatar,
      email,
      password
    }

    createUser(userData).then(response => {
      if (response.error) {
        console.log(response)
        setErrors(response.data)
      }
    })
  }
  // TODO Доделать Дополнительный номер !!! //
  const handleImgReset = () => {
    setAvatar(require('@src/assets/images/avatars/avatar-blank.png').default)
  }

  return (
    <Fragment>
      <Card>
        <CardHeader className='border-bottom'>
          <CardTitle tag='h4'>Создание пользователя</CardTitle>
        </CardHeader>
        <CardBody className='py-2 my-25'>
          <div className='d-flex'>
            <div className='me-25'>
              <img className='rounded me-50' src={avatar} alt='Generic placeholder image' height='100' width='100' />
            </div>
            <div className='d-flex align-items-end mt-75 ms-1'>
              <div>
                <Button tag={Label} className='mb-75 me-75' size='sm' color='primary'>
                  Загрузить
                  <Input type='file' onChange={onChange} hidden accept='image/*' />
                </Button>
                <Button className='mb-75' color='secondary' size='sm' outline onClick={handleImgReset}>
                  Очистить
                </Button>
                <p className='mb-0'>Поддерживаемые форматы: PNG, JPG, JPEG</p>
              </div>
            </div>
          </div>
          <Form className='mt-2 pt-50' onSubmit={(ev) => handleFormSubmit(ev)}>
            <Row>
              <Col sm='4' className='mb-1'>
                <PickerHumanFriendly onChange={(date) => setBirthDate(date)} />
              </Col>
              <Col sm='4' className='mb-1'>
                <Label className='form-label' for='country'>
                  Пол
                </Label>
                <Select
                  name='sex'
                  id='sex'
                  isClearable={false}
                  className='react-select'
                  classNamePrefix='select'
                  options={sexOptions}
                  theme={selectThemeColors}
                  defaultValue={sex}
                  onChange={(ev) => setSex(ev)}
                />
              </Col>
              <Col sm='4' className='mb-1'>
                <Label className='form-label' for='phNumber'>
                  Телефон
                </Label>
                <Cleave
                  required
                  id='phNumber'
                  name='phNumber'
                  className='form-control'
                  placeholder='7-999-888-77-66'
                  value={phone}
                  options={{blocks: [1, 3, 3, 2, 2], delimiter: '-', numericOnly: true}}
                  onChange={(ev) => setPhone(ev.target.value)}
                />
              </Col>
              <Col sm='4' className='mb-1'>
                <Label className='form-label' for='lastName'>
                  Имя
                </Label>
                <Input onChange={(ev) => setLastName(ev.target.value)} id='lastName' required placeholder='Иван' invalid={errors.lastName && true} />
                {errors.lastName && <FormFeedback>Please enter a valid Last Name</FormFeedback>}
              </Col>
              <Col sm='4' className='mb-1'>
                <Label className='form-label' for='firstName'>
                  Фамилия
                </Label>
                <Input onChange={(ev) => setFirstName(ev.target.value)} id='firstName' required placeholder='Иванов' invalid={errors.firstName && true} />
                {errors.firstName && <FormFeedback>Please enter a valid First Name</FormFeedback>}
              </Col>
              <Col sm='4' className='mb-1'>
                <Label className='form-label' for='middleName'>
                  Отчество
                </Label>
                <Input onChange={(ev) => setMiddleName(ev.target.value)} id='middleName' required placeholder='Иванович' invalid={errors.middleName && true} />
                {errors.middleName && <FormFeedback>Please enter a valid Middle Name</FormFeedback>}
              </Col>
              <Col sm='4' className='mb-1'>
                <Label className='form-label' for='emailInput'>
                  E-mail
                </Label>
                <Input invalid={errors.email} onChange={(ev) => { console.log(ev); setEmail(ev.target.value) }} defaultValue={email} id='emailInput' type='email' name='email' placeholder='Email@mail.com' />
                {errors.email && <FormFeedback invalid>{errors.email}</FormFeedback>}
              </Col>
              <Col sm='4' className='mb-1'>
                <Label className='form-label' for='password'>
                  Пароль
                </Label>
                <Input invalid={errors.password} onChange={(ev) => setPassword(ev.target.value)} defaultValue={password} id='password' name='password' placeholder='******' type='password' />
                {errors.password && <FormFeedback invalid>{errors.password}</FormFeedback>}
              </Col>
              <Col sm='4' className='mb-1 my-auto'>
                <div className='form-check form-switch form-check-primary' style={{marginBottom: '10px'}}>
                  <Input onChange={(ev) => setPasGen(ev.target.value)} type='switch' name='passGen' id='passGen' defaultChecked/>
                  <Label for='passGen' className='form-check-label'>
                    Генерация пароля
                  </Label>
                </div>
              </Col>
              <Col sm='12' className='mb-1'>
                <RepeatPhoneForm />
              </Col>
              <Col className='mt-2' sm='12'>
                <Button type='submit' className='me-1' color='primary'>
                  Создать ученика
                </Button>
                <Link to={'/manager/user/list/'}>
                  <Button color='secondary' outline>
                    Отмена
                  </Button>
                </Link>
              </Col>
            </Row>
          </Form>
        </CardBody>
      </Card>
    </Fragment>
  )
}

export default AccountForm
